.body-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    font-family: 'Quicksand', sans-serif;
    margin-bottom: 20px;
}

.input-container {
    margin-top: 50px;
    margin-bottom: 30px;
}

.input-container label {
    font-size: 16px;
    margin-right: 10px;
}

.input-container input {
    margin-top: 5px;
    margin-right: 10px;
    padding: 5px;
    font-size: 14px;
    width: 100px;
}

.input-container button {
    padding: 5px 10px;
    font-size: 14px;
    cursor: pointer;
    background-color: #008CBA;
    color: white;
    border: none;
    border-radius: 4px;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.input-container button:hover {
    background-color: #005f73;
    transform: translateY(-2px);
}

.input-container button:active {
    transform: translateY(1px);
}

.result-container {
    margin-top: 20px;
    animation: fadeIn 1s ease-out;
}

.result-container h3 {
    margin-bottom: 10px;
}

.procedure {
    margin-top: 30px;
    max-width: 600px;
    text-align: left;
}

.procedure h3 {
    margin-bottom: 10px;
}

.procedure ul {
    list-style-type: none;
    padding-left: 0;
}

.procedure li {
    margin-bottom: 5px;
    animation: listItemFadeIn 0.6s ease-out;
}

.procedure li:nth-child(1) {
    animation-delay: 0s;
}

.procedure li:nth-child(2) {
    animation-delay: 0.2s;
}

.procedure li:nth-child(3) {
    animation-delay: 0.4s;
}

.procedure li:nth-child(4) {
    animation-delay: 0.6s;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes listItemFadeIn {
    0% {
        opacity: 0;
        transform: translateX(-20px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

.procedure-container ul {
    list-style-type: none; /* Elimina los puntos o marcas */
    padding-left: 0; /* Elimina la sangría de la lista */
}

.procedure-container li {
    margin-bottom: 5px; /* Espaciado entre los elementos de la lista */
    animation: listItemFadeIn 0.6s ease-out;
}
