.procedure-container {
    background-color: #264653;
    padding: 20px;
    border-radius: 10px;
    margin-top: 20px;
    color: #ffb703;
    font-family: 'Poppins', sans-serif;
}

.procedure-container h3 {
    margin-bottom: 10px;
}

.procedure-container p {
    font-size: 1rem;
    margin: 10px 0;
}

.procedure-container ul {
    list-style-type: none;
    padding-left: 0;
}

.procedure-container ul li {
    margin: 5px 0;
    font-size: 1rem;
}
